import { Component, OnInit, ViewChild } from '@angular/core';
import { SlideInOutAnimation } from 'src/app/animations';
import { Slide } from "src/app/carousel/carousel.interface";
import { AnimationType } from "src/app/carousel/carousel.animations";
import { CarouselComponent } from "src/app/carousel/carousel.component";
import { job } from 'src/app/job';

@Component({
  selector: 'app-work',
  templateUrl: './work.component.html',
  styleUrls: ['./work.component.css'],
  animations: [SlideInOutAnimation]
  
})
export class WorkComponent implements OnInit {
  work: job[] = [ 
    {id: 1, role: "Team Lead", company: "Jazz Loft", time: "February 2020 - Present", location: "Stony Brook, NY", 
    details: ["Led the Front End Team of 2 and planned the project with our client to define scope, tasks, and acceptance criterias for the user features, design and display.", "Established weekly communication with my team members, client, database and digitation teams, and created wireflow and wireframes.", "Organized demo presentations and set up collaborative coding sessions to address team memberâ€™s blockage."],
    url: "../assets/slides/Wires/Jazz Loft Design Brief  - WireFlow.png"},
    {id: 2, role: "Developer", company: "Jazz Loft", time: "February 2020 - Present", location: "Stony Brook, NY", 
    details: ["Built 5 pages and their features using Angular, TypeScript, HTML, and CSS.", "Implemented user authentication and a temporary database using Angular's In-Memory Service to intercept HTTP and HTTPClient requests and redirect them to an in-memory data store.", "Researched and documented the database team's FileMaker API, which will be used to replace our In-Memory Service when the actual database completes."],
    url: "../assets/slides/Wires/codeSample.png"},
  ];
  teamRole: any;
  teamLead: boolean;
  developer: boolean;
  show: boolean;

  @ViewChild(CarouselComponent, {static: true}) carousel: CarouselComponent;

  animationType = AnimationType.Scale;

  animationTypes = [
    {
      name: "Scale",
      value: AnimationType.Scale
    },
    {
      name: "Fade",
      value: AnimationType.Fade
    },
    {
      name: "Flip",
      value: AnimationType.Flip
    },
    {
      name: "Jack In The Box",
      value: AnimationType.JackInTheBox
    }
  ];
  slides: Slide[] = [
    {
      headline: "Jazz Loft Online Archive Home Page",
      src:
        "../assets/slides/New Slides/Home.png",
      alt: "First Slide"
    },
    {
      headline: "Jazz Loft Online Archive (Unfiltered) Page",
      src:
        "../assets/slides/New Slides/Archive_Unfiltered.png",
        alt: "Second Slide"
    },
    {
      headline: "Jazz Loft Online Archive (Filtered) Page",
      src:
        "../assets/slides/New Slides/Archive_Music Sheet.png",
        alt: "Third Slide"
    },
    {
      headline: "Jazz Loft Online Item Page",
      src:
        "../assets/slides/New Slides/Item.png",
        alt: "Third Slide"
    },
    {
      headline: "Jazz Loft Online Archive Staff Page",
      src:
        "../assets/slides/New Slides/Staff.png",
      alt: "Fourth Slide"
    },
    {
      headline: "Jazz Loft Online Archive Admin Page",
      src:
        "../assets/slides/New Slides/Admin.png",
      alt: "Fifth Slide"
    },
  ];

  constructor() {}

  setAnimationType(type) {
    this.animationType = type.value;
    setTimeout(() => {
      this.carousel.onNextClick();
    });
  }

  ngOnInit() {
    this.teamLead = false;
    this.developer = false;
    this.show = true;
    this.teamRole = this.work.find(
      obj => {
        return obj.id === 1
      }
    )
  }
  // toggleShow() {
  //   this.show = !this.show;
  // }
  leadButton: string = "Team Lead";
  developerButton: string = "Developer";
  showLess: string = "Show Less...";
  setRole(id) {

    if (id === 1) {
      if (this.teamLead === true) { // clicked same consequtively
        this.leadButton = "Team Lead";
        this.show = !this.show;
        this.teamLead = !this.teamLead;
        this.animationState = this.animationState === 'out' ? 'in' : 'out';
      } 
      else { // clicked once
        this.leadButton = this.showLess;
        if (this.developer === false) { // first click
          this.teamLead = !this.teamLead;
          this.show = !this.show;
          this.animationState = this.animationState === 'out' ? 'in' : 'out';
        }
        else { // 2nd click
          this.developerButton = "Developer";
          this.teamLead = !this.teamLead;
          this.developer = !this.developer;
        } 
      } 
    }

    else {
      if (this.developer === true) { // clicked same consequtively
        this.developerButton = "Developer";
        this.show = !this.show;
        this.developer = !this.developer;
        this.animationState = this.animationState === 'out' ? 'in' : 'out';
      } 
      else { // clicked once
        this.developerButton = this.showLess;
        if (this.teamLead === false) { // first click
          this.developer = !this.developer;
          this.show = !this.show;
          this.animationState = this.animationState === 'out' ? 'in' : 'out';
        }
        else { // 2nd click
          this.leadButton = "Team Lead";
          this.developer = !this.developer;
          this.teamLead = !this.teamLead;
        } 
      }
    }

    this.teamRole = this.work.find(
      obj => {
        return obj.id === id
      }
    )
  }
  name = 'Angular 5';
  animationState = 'out';

  toggleShowDiv(divName: string) {
    if (divName === 'divA') {
      console.log(this.animationState);
      this.animationState = this.animationState === 'out' ? 'in' : 'out';
      console.log(this.animationState);
    }
  }
}
